import Scroll from '../../../base/scroll/scroll.vue'
import Step from '../../../base/step/step.vue'
import Upload from '../../../base/upload/upload.vue'

const typeArr = ['', '', '', '', '']
export default {
  name: 'getPrice1',
  components: {
    Step,
    Upload,
    Scroll
  },
  data () {
    return {
      stepArr: ['', ''],
      stepIndex: 0,
      isOk: true, // 
      openIt: false, // 
      isPass: true, // 
      isRight: false, // 
      isWrong: false, // 
      goodsOneKey: '',
      goodsTwoKey: '',
      goodsThreeKey: '',
      shopName: '',
      twoInfo: {},
      task: [],
      taskIndex: 0,
      showMsg: {},
      seller: ''
    }
  },
  //    
  created () {
    let that = this
    this.$axios.post('/api/orderOperate/listSellerTaskKeyword', {
      sellerTaskId: that.$route.query.sellerTaskId
    }).then((response) => {
      let data = response.data
      if (data.code === '200') {
        let arr = data.data
        this.task = arr
        this.taskIndex = this.mathRandom()
        this.changeMsg()
      }
    }).catch((error) => {
      console.log(error)
    })

    this.$axios.post('/api/orderOperate/getBuyerTask', {
      buyerTaskRecordId: that.$route.query.buyerTaskRecordId
    }).then((res) => {
      console.log(res)
      let data = res.data
      if (data.code === '200') {
      }
    }).catch(() => {
      this.$vux.alert.show({
        title: '',
        content: ''
      })
    })
    //      
    this.$axios.post('/api/orderOperate/getAdditionalInfo', {
      buyerTaskRecordId: that.$route.query.buyerTaskRecordId
    }).then((res) => {
      let data = res.data
      if (data.code === '200') {
        this.twoInfo = data.data
      }
    }).catch(() => {
      this.$vux.alert.show({
        title: '',
        content: ''
      })
    })
  },
  methods: {
    open () {
      this.openIt = !this.openIt
      // 
      this.$nextTick(() => {
        this.$refs.scroll.refresh()
      })
    },
    // taskindex
    // 
    mathRandom () {
      let length = this.task.length
      return Math.floor((Math.random() * length))
    },
    // 
    changeMsg () {
      let nowMsg = this.task[this.taskIndex]
      console.log(nowMsg)
      this.showMsg.discountsKeyword = JSON.parse(nowMsg.discountsKeyword).join(' ')
      this.showMsg.sendPlace = nowMsg.sendPlace
      // 
      this.showMsg.sortType = typeArr[(parseInt(nowMsg.sortType) - 1)]
      //      
      this.showMsg.price = (nowMsg.priceLower) + '-' + (nowMsg.priceLower) + ''
      this.showMsg.sellerTaskId = nowMsg.sellerTaskId
      var keyWordObj = JSON.parse(nowMsg.keyword)
      let keyName = ''
      for (let m in keyWordObj) {
        keyName += `${m} `
      }
      this.showMsg.keyName = keyName
      console.log(this.showMsg)
    },
    onInput () {
      // 
      if (this.seller === this.twoInfo.shopName) {
        this.isPass = false
        this.isRight = true
        this.isWrong = false
      } else {
        this.isPass = false
        this.isRight = false
        this.isWrong = true
      }
    },

    showIsPass () {
      this.isPass = true
      this.isRight = false
      this.isWrong = false
    }
  }
}



